import api from "../../../../../constants/api";
import Service from "../../../../../services/request";

export const getListCategoryProduct = (params) => {
  return async (dispatch) => {
    await Service.send({
      method: api.LIST_CATEGORY_PRODUCT.method,
      path: api.LIST_CATEGORY_PRODUCT.path,
      data: params,
    }).then((response) => {
      dispatch({
        type: "GET_DATA_CATEGORY_PRODUCT",
        data: response?.data?.data,
        totalPages: response?.data?.total,
        params,
      });
    });
  };
};

export const getCategoryProduct = (id) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_CATEGORY_PRODUCT.method,
      path: api.GET_CATEGORY_PRODUCT.path,
      query: { id },
    })
      .then((response) => {
        dispatch({
          type: "GET_CATEGORY_PRODUCT",
          selected: response?.data,
        });
      })
      .catch((err) => console.log(err));
  };
};

export const addCategoryProduct = (CategoryProduct) => {
  return async (dispatch) => {
    await Service.send({
      method: api.CREATE_CATEGORY_PRODUCT.method,
      path: api.CREATE_CATEGORY_PRODUCT.path,
      data: CategoryProduct,
    })
      .then((response) => {
        dispatch({
          type: "ADD_CATEGORY_PRODUCT",
          response,
        });
      })
      .catch((err) =>
        dispatch({
          type: "ADD_CATEGORY_PRODUCT",
          err,
        })
      );
  };
};
export const updateCategoryProduct = (CategoryProduct) => {
  return async (dispatch) => {
    await Service.send({
      method: api.UPDATE_CATEGORY_PRODUCT.method,
      path: api.UPDATE_CATEGORY_PRODUCT.path,
      data: CategoryProduct,
    })
      .then((response) => {
        dispatch({
          type: "UPDATE_CATEGORY_PRODUCT",
          response,
        });
      })

      .catch((err) =>
        dispatch({
          type: "UPDATE_CATEGORY_PRODUCT",
          err,
        })
      );
  };
};
